import React from 'react';
import {render} from 'react-dom';
import logo from './logo.svg';
import './App.css';

class App extends React.Component {

  constructor(props) {
    super(props);
    this.state={
      items:[],
      isLoaded: false,
    }
  }

  componentDidMount(){

    fetch('https://anapioficeandfire.com/api/houses')
    .then(res => res.json())
    .then(json => {
      this.setState({
        isLoaded: true,
        items: json,
      })
    });
  }

  render(){

    let {isLoaded, items} = this.state;

    if(!isLoaded){
      return <div>Loading...</div>;
    } 
    else {
        return (
          <div className="App">
            <h1 className='text-4xl'>An API of Ice And Fire</h1>
            <div>
              
              {items.map(item => (
                <> 

                  <div class="bg-gray-100 p-4">
  <div class="bg-white border rounded-sm max-w-md">
    <div class="flex items-center px-4 py-3">
      <img class="h-8 w-8 rounded-full" src="https://pyxis.nymag.com/v1/imgs/6c3/0ba/af83c5129dcdb0b0d89e142efe0e0b8fef-13-the-witcher.rsquare.w700.jpg"/>
      <div class="ml-3 ">
        <span class="text-sm font-semibold antialiased block leading-tight">{item.name}</span>
        <span class="text-gray-600 text-xs block">{item.region}</span>
      </div>
      </div>
    <div class="flex items-center justify-between mx-4 mt-3 mb-2">
      <div class="flex gap-5">
      <em>{item.coatOfArms}</em>
      </div>
      <div class="flex">
      </div>
    </div>
    <div class="font-semibold text-sm mx-4 mt-2 mb-4">        <span class="text-gray-600 text-xs block"><i>{item.words}</i></span>
</div>
  </div>
</div>
                </>
              ))}
            </div>

          </div>
        )
    }
    
  }
}

export default App;
